
<!--注册框-->

<template>
  <div class="login-form">
    <div class="g-form">
      <div class="g-form-line">
        <span class="g-form-label">创建账号:</span>
        <div class="g-form-input">
          <input type="text" placeholder="请输入账号">
        </div>
      </div>
      <div class="g-form-line">
        <span class="g-form-label">创建密码:</span>
        <div class="g-form-input">
          <input type="password" placeholder="请输入密码">
        </div>
      </div>
      <div class="g-form-line">
        <span class="g-form-label">重复密码:</span>
        <div class="g-form-input">
          <input type="password" placeholder="请确认密码">
        </div>
      </div>
      <div class="g-form-line">
        <div class="g-form-btn">
          <a class="button" @click="confirm">确认注册</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "regLog",
    methods: {
      confirm () {
        this.$emit('has-reg');
      }
    }
  }
</script>

<style scoped>

</style>



