<template>
  <div>
    <this-dialog :is-show="isShowCheckOrder" @on-clost="checkStatus">
      请检查您的支付状态!
      <div class="button" @click="checkStatus">支付成功</div>
      <div class="button" @click="checkStatus">支付失败</div>
    </this-dialog>
    <this-dialog :is-show="isShowSuccess" @on-close="toOrderList">
      购买成功!
    </this-dialog>
    <this-dialog :is-show="isShowFailed" @on-close="toOrderList">
      购买失败!
    </this-dialog>
  </div>
</template>

<script>
  import Dialog from './dialog';

  export default {
    name: "checkOrder",
    components: {
      thisDialog: Dialog
    },
    data() {
      return {
        isShowSuccess: false,
        isShowFailed: false
      }
    },
    props: {
      isShowCheckOrder: {
        type: Boolean,
        default: false
      },
      orderId: {
        type: [String, Number]
      }
    },
    methods: {
      checkStatus() {
        this.$http.post('/api/checkOrder', {
          orderId: this.orderId
        })
          .then(res => {
            this.isShowSuccess = true;
            this.$emit('on-close-check-dialog');
          })
          .catch(err => {
            this.isShowFailed = true;
            this.$emit('on-close-check-dialog');
          })
      },
      toOrderList () {
        this.$router.push({path: '/orderList'});
      }
    }
  }
</script>

<style scoped>

</style>



