
<!--登录框-->

<template>
  <div class="login-form">
    <div class="g-form">
      <div class="g-form-line">
        <span class="g-form-label">账号:</span>
        <div class="g-form-input">
          <input type="text" v-model="usernameModel" placeholder="请输入您的账号">
        </div>
        <span class="g-form-error">{{usernameErrors.errorText}}</span>
      </div>
      <div class="g-form-line">
        <span class="g-form-label">密码:</span>
        <div class="g-form-input">
          <input type="password" v-model="passwordModel" placeholder="请输入您的密码">
        </div>
        <span class="g-form-error">{{passwordErrors.errorText}}</span>
      </div>
      <div class="g-form-line">
        <div class="g-form-btn">
          <a class="button" @click="onLogin">登录</a>
        </div>
      </div>
      <p>{{statusText}}</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: "logForm",
    data() {
      return {
        usernameModel: '',
        passwordModel: '',
        statusText: '',
      }
    },
    computed: {
      usernameErrors () {
        let errorText,status;
        if (!/@/g.test(this.usernameModel)) {
          status = false;
          errorText = '账号中没有包含@';
        } else {
          status = true;
          errorText = '';
        }
        if (!this.usernameFlag) {
          errorText = '';
          this.usernameFlag = true;
        }
        return {
          status,
          errorText
        }
      },
      passwordErrors () {
        let errorText,status;
        if (!/^\w{6,13}$/g.test(this.passwordModel)) {
          status = false;
          errorText = '密码不是6-13位';
        } else {
          status = true;
          errorText = '';
        }
        if (!this.passwordFlag) {
          errorText = '';
          this.passwordFlag = true;
        }
        return {
          status,
          errorText
        }
      }
    },
    methods: {
      onLogin () {
        if (!this.usernameErrors.status || !this.passwordErrors.status) {
          this.statusText = '账号或密码验证未通过';
        } else {
          /*this.errorText = '';
          this.$http.get('api/login')
            .then(res => {
              this.$emit('has-log', res.data)
            })
            .catch(err => {
              console.log(err);
            })*/
          this.$emit('has-log', {username: 'username'});
          this.statusText = '登录成功';
        }
      }
    }
  }
</script>

<style scoped>

</style>



