
<!--头部布局-->

<template>
  <div>
    <div class="app-head">
      <div class="app-head-inner">
        <router-link :to="{path:'/'}">
          <img src="../assets/logo.png">
        </router-link>
        <div class="head-nav">
          <ul class="nav-list">
            <li>{{username}}</li>
            <li v-if="username !== ''" class="nav-pile">|</li>
            <li v-if="username === ''" @click="logClick">登录</li>
            <li v-if="username !== ''" @click="quitLog">退出</li>
            <li v-if="username !== ''" class="nav-pile">|</li>
            <li v-if="username === ''" class="nav-pile">|</li>
            <li v-if="username === ''" @click="regClick">注册</li>
            <li v-if="username === ''" class="nav-pile">|</li>
            <li @click="aboutClick">关于</li>
          </ul>
        </div>
      </div>
    </div>
    <div class="app-content">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <div class="app-foot">
      <p>&copy; 2018 WangLiChao is here</p>
    </div>
    <!--登录框-->
    <my-log :is-show="isShowLog" @on-close="closeDialog('isShowLog')">
      <log-form @has-log="onSuccessLog"></log-form>
    </my-log>
    <!--注册框-->
    <my-log :is-show="isShowReg" @on-close="closeDialog('isShowReg')">
      <reg-form @has-reg="onsuccessReg('isShowReg')"></reg-form>
    </my-log>
    <!--关于框-->
    <my-log :is-show="isShowAbout" @on-close="closeDialog('isShowAbout')">
      <p>我是王立超,一个前端菜鸟。</p>
      <p>我是王立超,一个前端菜鸟。</p>
      <p>我是王立超,一个前端菜鸟。</p>
      <p>我是王立超,一个前端菜鸟。</p>
      <p><a href="https://blog.csdn.net/wangwangwanglichao">我的博客</a></p>
    </my-log>
  </div>
</template>
<script>
  import myLog from './dialog';
  import logForm from './logForm';
  import regForm from './regLog';
  export default {
    name: "layout",
    components: {
      myLog,
      logForm,
      regForm,
    },
    data () {
      return {
        isShowLog: false,
        isShowReg: false,
        isShowAbout: false,
        username: '',
      }
    },
    methods: {
      /* 打开登录框 */
      logClick () {
        this.isShowLog = true;
      },
      /* 打开注册框 */
      regClick () {
        this.isShowReg = true;
      },
      /* 打开关于框 */
      aboutClick () {
        this.isShowAbout = true;
      },
      /* 关闭弹出框 */
      closeDialog (attr) {
        this[attr] = false;
      },
      /* 登陆成功 */
      onSuccessLog (data) {
        this.closeDialog('isShowLog');
        this.username = data.username;
      },
      /* 注册成功 */
      onsuccessReg (str) {
        this.closeDialog(str);
      },
      /* 退出登录 */
      quitLog () {
        this.username = '';
      }
    }
  }
</script>
<style>
  /* style reset */
  html, body, div, span, applet, object, iframe,
  h1, h2, h3, h4, h5, h6, p, blockquote, pre,
  a, abbr, acronym, address, big, cite, code,
  del, dfn, em, img, ins, kbd, q, s, samp,
  small, strike, strong, sub, sup, tt, var,
  b, u, i, center,
  dl, dt, dd, ol, ul, li,
  fieldset, form, label, legend,
  table, caption, tbody, tfoot, thead, tr, th, td,
  article, aside, canvas, details, embed,
  figure, figcaption, footer, header, hgroup,
  menu, nav, output, ruby, section, summary,
  time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    vertical-align: baseline;
  }

  /* HTML5 display-role reset for older browsers */
  article, aside, details, figcaption, figure,
  footer, header, hgroup, menu, nav, section {
    display: block;
  }

  body {
    line-height: 1;
  }

  ol, ul {
    list-style: none;
  }

  blockquote, q {
    quotes: none;
  }

  blockquote:before, blockquote:after,
  q:before, q:after {
    content: '';
  }

  table {
    border-collapse: collapse;
    border-spacing: 0;
  }

  a {
    color: darkcyan;
    text-decoration: none;
  }

  a:hover {
    color: #f40;
    text-decoration: underline;
  }

  body {
    background: #f0f2f5;
    font-family: "Helvetica Neue", Helvetica, Arial, "Hiragino Sans GB", "Hiragino Sans GB W3", "Microsoft YaHei UI", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
    font-size: 14px;
    color: #444;
  }

  /* header start*/
  .app-head {
    background-color: #363636;
    color: #b2b2b2;
    height: 90px;
    line-height: 90px;
    width: 100%;
  }

  .app-head-inner {
    width: 1200px;
    margin: 0 auto;
  }

  .head-logo {
    float: left;
  }

  .app-head-inner img {
    width: 50px;
    margin-top: 20px;
  }

  .head-nav {
    float: right;
  }

  .head-nav ul {
    overflow: hidden;
  }

  .head-nav li {
    cursor: pointer;
    float: left;
  }

  .nav-pile {
    padding: 0 10px;
  }

  /* header end */

  /* container start */
  .container {
    width: 1200px;
    margin: 0 auto;
  }

  .hr {
    height: 1px;
    width: 100%;
    background: #ddd;
  }

  .button {
    background: #4fc08d;
    color: #fff;
    display: inline-block;
    padding: 10px 20px;
    cursor: pointer;
  }

  .button:hover {
    background: #4fc08d;
  }

  .g-form {

  }

  .g-form-line {
    padding: 15px 0;
  }

  .g-form-label {
    width: 100px;
    font-size: 16px;
    display: inline-block;
  }

  .g-form-input {
    display: inline-block;
  }

  .g-form-input input {
    height: 30px;
    width: 200px;
    line-height: 30px;
    vertical-align: middle;
    padding: 0 10px;
    border: 1px solid #ccc;
  }

  .g-form-btn {
    padding-left: 100px;
  }

  .g-form-error {
    color: red;
    padding-left: 15px;
  }

  /* container end */

  /* footer start */
  .app-foot {
    text-align: center;
    height: 80px;
    width: 100%;
    line-height: 80px;
    background-color: #e3e4e9;
    clear: both;
    margin-top: 30px;
  }
  /* footer end */

</style>


































